import { FunctionComponent } from 'react';
import styled, { css } from 'styled-components';

import { Flex, Icon, Actions, Text, defaultThemeProp } from '@pega/cosmos-react-core';

import { ArticleListHeaderProps } from './ArticleList.types';

const StyledHeaderText = styled(Text)`
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  flex-grow: 1;
`;

const StyledIcon = styled(Icon)(
  ({
    theme: {
      base: { spacing }
    }
  }) => {
    return css`
      min-width: calc(3 * ${spacing});
    `;
  }
);

StyledIcon.defaultProps = defaultThemeProp;

const ArticleListHeader: FunctionComponent<ArticleListHeaderProps> = ({ icon, title, actions }) => {
  return (
    <Flex container={{ alignItems: 'center', gap: 1, pad: 2 }}>
      {icon && <StyledIcon name={icon} />}
      <StyledHeaderText variant='h2'>{title}</StyledHeaderText>
      {actions && <Actions items={actions} />}
    </Flex>
  );
};

export default ArticleListHeader;
